.app {
  text-align: center;
  background-color: #ffc107;
  height: 93vh;
  overflow: scroll;
}

.footer{
  display: flex;
  justify-content: center;
  background-color: black;
  color: #ffc107;
  height: 7vh;
  padding-top: 1em;
 
}

.tag-container {
  display: flex;
  overflow: scroll;
  width: 100%;
  max-width: 100%;
  padding-left: 14px;
  border: 1px grey solid;
  border-radius: 5px;
  color: black
}

.tag-container input {
  width: 100%;
  min-width: 50%;
  border: none;
  border-radius: 5px;
  padding: 14px;
  padding-left: 14px;
}

.tag {
  display: flex;
  align-items: center;
  margin: 7px 0;
  margin-right: 10px;
  padding: 0 10px;
  padding-right: 5px;
  border: 1px solid orange;
  border-radius: 5px;
  background-color: orange;
  white-space: nowrap;
  color: white;
  max-width: 10em;
  overflow: scroll;
}

.tag button {
  display: flex;
  padding: 6px;
  border: none;
  background-color: unset;
  cursor: pointer;
  color: white;
}
.sort-button {
  display:inline-block;
  padding: 0.3em 1.2em;
  margin:0 0.3em 0.3em 0;
  border-radius:2em;
  box-sizing: border-box;
  text-decoration:none;
  font-family: 'Roboto',sans-serif;
  font-weight:300;
  color:#FFFFFF;
  background-color:#000203;
  text-align:center;
  transition: all 0.2s;
}

.sort-button:hover {
background-color:#4095c6;
}

.table-hover :hover {
  /* background-color: coral; */
  cursor: pointer;
}

.table-styles {
  
  max-width: auto;
  
  overflow: scroll;
 
}

.table-row{
  max-height: 100px;
  overflow: scroll;
}

html {
  font: 1em sans-serif;
}
@media screen and (max-width: 768px) {
  html {
    font-size: 0.6rem;
  }

}
@media screen and (max-width: 459px) {
  html {
    font-size: 0.4rem;
  }

}